@import "src/common/styles/sass/variables.scss";
@import "src/common/components/button/Button.module";

.contactsBlock {
  padding: 30px 0;
  background-color: $bgSecondColor;

  .contactsContainer {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;

    .form {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: stretch;
      flex-wrap: nowrap;
      width: 540px;
      margin-top: 60px;
      @media screen and (max-width: 680px) {
        width: 100%;
      }

      .formArea,
      .messageArea {
        background: $bgSecondColor;
        border: 1px solid #4a4a4a;
        box-sizing: border-box;
        font-family: inherit;
        font-size: 12px;
        line-height: 1.5;
        font-weight: 400;
        outline: none;
        color: #495057;
        margin-bottom: 1.2rem;

        &:-webkit-autofill {
          border: 1px solid #4a4a4a;
          -webkit-text-fill-color: #495057;
          -webkit-box-shadow: 0 0 0 1000px #252257 inset;
          transition: background-color 5000s ease-in-out 0s;

          &:focus {
            border: 2px solid $bgSecondColor;
          }
        }
      }

      .formArea {
        height: calc(1.5em + .75em + 2px);
        //padding: 4rem 15px;

        &:focus {
          border: 1px solid transparent;
          transition: all 0.4s ease-in-out 0s;
        }
      }

      .messageArea {
        min-height: 160px;
        padding: 15px;
        resize: none;

        &:focus {
          border: 1px solid transparent;
          transition: all 0.4s ease-in-out 0s;
        }
      }

      button[type="submit"] {
        @extend .btn;
        align-self: center;
        background-color: transparent;
        margin-top: 30px;
      }
      //#button {
      //  background-color: red
      //}
      //.button {
      //  background-color: #0066ff
      //}
      //button {
      //  background-color: #2fff00
      //}
    }
  }
}

