@import "src/common/styles/sass/variables";

.mainBlock {
  display: flex;
  min-height: 90vh;
  background-color: $bgColor;
  flex-direction: column-reverse;
  justify-content: center;

  .mainContainer {
    //display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: nowrap;
    @media screen and (max-width: 1100px) {
      flex-direction: column;
      justify-content: center;
    }

    .text {
      width: 300px;
      background-color: $bgColor;

      .hello {
        font-family: 'Poppins', sans-serif;
        font-weight: 400;
        color: $primaryColor;
        font-size: 36px;
        line-height: 0.8;
        @media screen and (max-width: 690px) {
          font-size: 27px
        }
      }

      h1 {
        font-family: 'Playfair Display', serif;
        font-size: 90px;
        font-weight: 500;
        color: $textColor;
        margin: 0;
        line-height: 1.1;
        letter-spacing: 1px;
        @media screen and (max-width: 690px) {
          font-size: 67px
        }
      }

      p {
        font-family: 'Poppins', sans-serif;
        font-weight: 400;
        color: $textColor;
        font-size: 22px;
        @media screen and (max-width: 690px) {
          font-size: 17px
        }
      }
    }

    .photo {
      width: 300px;
      height: 400px;
      background-color: #f7f7f7;
      @media screen and (max-width: 690px){
        width: 225px;
        height: 300px;
      }
    }
  }
}


